import React, { Component } from 'react'
import Button from '@eui/ic-button'
import Popup from '@eui/ic-popup'
import Markdown from 'ic-markdown'
import code from './index.md'
class App extends Component {
  state = { show: false }
  clickHanlder = () => { this.setState({ show: !this.state.show }) }
  createPopup = () => Popup.info({
    content: '', title: '', afterClose: () => { console.log('closed') }
  })
  render() {
    return (
      <section className='section-ic-btn'>
        <section>
          <Popup visible={this.state.show} title={''} onOk={this.clickHanlder} onCancel={this.clickHanlder}></Popup>
          <Button onClick={this.clickHanlder} >toggle</Button>{' '}
          <Button onClick={this.createPopup}>info</Button>
        </section>
        <Markdown>{code}</Markdown>
      </section>
    )
  }
}

export default App



