import React, {PureComponent, Fragment} from 'react';
import Scroll from '@eui/ic-scroll';
import Markdown from 'ic-markdown';
import defaultMD from './default.md';
import lightMD from './light.md';
import lighterMD from './lighter.md';
import noneMD from './none.md';
import configMD from './config.md';
import './index.css';

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section></section>
                <section>
                    <p>typedefault</p>
                    <Scroll>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{defaultMD}</Markdown>
                </section>
                <section>
                    <p>typelight</p>
                    <Scroll type='light'>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{lightMD}</Markdown>
                </section>
                <section>
                    <p>typelighter</p>
                    <Scroll type='lighter'>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{lighterMD}</Markdown>
                </section>
                <section>
                    <p></p>
                    <Scroll>
                        <div></div>
                        <div></div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{noneMD}</Markdown>
                </section>
                <section>
                    <p>better-scrollAPIconfig</p>
                    <Scroll config={{startY: -30}}>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{configMD}</Markdown>
                </section>
            </Fragment>
        );
    }
}




