import React, { PureComponent,Fragment} from 'react'
import IcTag from '@eui/ic-tag'
import Markdown from 'ic-markdown'
import keywordTag from './keywordTag.md'
import closeTag from './closeTag.md'

export default class extends PureComponent {

    render() {
        return (
            <Fragment>
                <section></section>
                <section>
                    <IcTag className="ic-tag-wrapper" color="#11AABB"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#3B99FF"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#FC3333"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#ffa000"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#ffa000" bgColor="#ffa000"></IcTag>
                </section>
                <section>
                    <Markdown>{keywordTag}</Markdown>
                </section>
                <section></section>
                <section>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                </section>
                <section>
                    <Markdown>{closeTag}</Markdown>
                </section>
            </Fragment>
        );
    }
}



