import React, { PureComponent,Fragment} from 'react'
import IcInput from '@eui/ic-input'
import IcIcon from '@eui/ic-iconfont'
import Markdown from 'ic-markdown'
import largeInput from './large.md'
import defaultInput from './default.md'
import smallInput from './small.md'
import numberInput from './number.md'

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section className="single-icon">large</section>
                <section>
                    <IcInput size="large" placeholder = "" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" size="large"></IcInput>
                </section>
                <section>
                    <IcInput size="large" placeholder = "" width={300} endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" size="large" width="400"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {largeInput}
                    </Markdown>
                </section>
                <section>default</section>
                <section>
                    <IcInput placeholder = "" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = ""></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" width="300" endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" width="400px"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {defaultInput}
                    </Markdown>
                </section>
                <section>small</section>
                <section>
                    <IcInput placeholder = "" size="small" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" size="small"></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" size="small" width="300px" endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "" size="small" width="400"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {smallInput}
                    </Markdown>
                </section>
                <section></section>
                <section><IcInput type = 'number'></IcInput></section>
                <section><IcInput width="200" type = 'number'></IcInput></section>
                <section><IcInput width="400" type = 'number' max = '100' min = '0'></IcInput></section>
                <section>
                    <Markdown>
                        {numberInput}
                    </Markdown>
                </section>
            </Fragment>
        );
    }
}



