import React, {PureComponent} from 'react'
import Form from 'ic-form'
import {Input, Textarea, Switch, RadioGroup,Checkbox} from '@eui/ic-field-decorator'
import Radio from '@eui/ic-radio'
import SubmitButton from '@eui/ic-submit-button'

export default class extends PureComponent {
    handlerSubmit = (data) => {
        console.log("", data);
    };

    render() {
        return (
            <Form onSubmit={this.handlerSubmit}>
                <Input name="name" label="" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="" rule="REQ LEN-6-12"/>
                <Textarea name="des" label="" rule="REQ"/>
                <Switch name="sex" label=""/>
                <RadioGroup name="type" label="">
                    <Radio value={0}></Radio>
                    <Radio value={1}></Radio>
                </RadioGroup>
                <SubmitButton></SubmitButton>
            </Form>
        );
    }
}

export class CustomRules extends PureComponent {
    handlerSubmit = (data) => {
        console.log("", data);
    };

    render() {
        return (
            <Form onSubmit={this.handlerSubmit} rules={{
                NUM: (value) => {
                    return {
                        result: !Number.isNaN(Number(value)),
                        errMsg: '%s'
                    };
                },
                IS_EXIST: (value) => {
                    return new Promise((resolve) => {
                        const phoneSet = ['18000000000', '18000000001', '18000000002'];
                        setTimeout(() => resolve({
                            result: value && phoneSet.indexOf(value) > -1,
                            errMsg: '%s'
                        }), 1000);
                    });
                }
            }}>
                <Input name="name" label="" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="" rule="REQ NUM LEN-6-12"/>
                <Input name="tel" label="" rule="REQ TEL IS_EXIST"/>
                <SubmitButton></SubmitButton>
            </Form>
        );
    }
}



