import React, {PureComponent, Fragment} from 'react';
import Switch from '@eui/ic-switch';
import Markdown from 'ic-markdown';
import switchMD from './switch.md';

function onChange(e, checked) {
    console.log(`switch to ${checked}`);
}

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section>
                    <Switch onChange={onChange}/> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="" uncheckedLabel="" /> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} disabled={true} /> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="" uncheckedLabel="" disabled /> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="" uncheckedLabel="" size="small" /> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={true} checkedLabel="" uncheckedLabel="" loading /> <br />
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="" uncheckedLabel="" loading /> <br />
                </section>
                <section>
                    <Markdown>{switchMD}</Markdown>
                </section>
            </Fragment>
        );
    }
}




