import React, {PureComponent} from 'react'
import Form from 'ic-form'
import {Input} from '@eui/ic-field-decorator'
import SubmitButton from '@eui/ic-submit-button'

export default class extends PureComponent{
    handlerSubmit=(data)=>{
        return new Promise((resolve)=>{
            console.log('');
            setTimeout(()=>{
                console.log(data);
                console.log('');
                resolve();
            },1000);
        });
    };
    render(){
        return (
            <Form onSubmit={this.handlerSubmit}>
                <Input name="name" label="" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="" rule="REQ LEN-6-12"/>
                <SubmitButton></SubmitButton>
            </Form>
        );
    }
}

export class RealTime extends PureComponent{
    handlerSubmit=(data)=>{
        console.log(data);
    };
    render(){
        return (
            <Form onSubmit={this.handlerSubmit}>
                <Input name="name" label="" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="" rule="REQ LEN-6-12"/>
                <SubmitButton realTime></SubmitButton>
            </Form>
        );
    }
}



