import React, {PureComponent, Fragment} from 'react'
import Markdown from 'ic-markdown'
import IcTextarea from '@eui/ic-textarea'
import textareaMd from './textarea.md'

export default class App extends PureComponent {
    handleValue =(value) =>{
        console.log('',value);
    }
    render() {
        return (
            <Fragment>
                <section>
                    <IcTextarea  placeholder="" disabled={true}></IcTextarea>
                </section>
                <section>
                    <IcTextarea  placeholder="" width="300" height = "200"></IcTextarea>
                </section>
                <section>
                    <IcTextarea placeholder="500"  width={300} height={200} maxLength = "500"></IcTextarea>
                </section>
                <section>
                    <IcTextarea maxLength = "500" onChange = {this.handleValue}></IcTextarea>
                </section>
                <section>
                    <Markdown>{textareaMd}</Markdown>
                </section>
            </Fragment>
        );
    }
}



