import React, { PureComponent,Fragment} from 'react'
import IcTagInput from '@eui/ic-tag-input'
import Markdown from 'ic-markdown'
import tagMd from './tagInput.md'

export default class extends PureComponent {
    handleData = (data) =>{
        console.log(data)
    }
    render() {
        return (
            <Fragment>
                <section></section>
                <section>
                    <section><IcTagInput placeholder = "" width = {300} maxHeight={100} maxLength = {20}></IcTagInput></section>
                    <section><IcTagInput placeholder = "" width = {300} maxHeight={100} onChange = {this.handleData}></IcTagInput></section>
                    <section><IcTagInput tagList = {["e",'','']} placeholder = "" value = ""></IcTagInput></section>
                </section>
                <section>
                    <Markdown>{tagMd}</Markdown>
                </section>
            </Fragment>
        );
    }
}



